<template>
  <div class="qrcode">
    <div class="code">
      <!-- decodetorchinitcamera  -->
      <qrcode-drop-zone @decode="onDecode">
        <qrcode-stream @decode="onDecode" :torch="torchActive" @init="onInit" :camera="camera"/>
      </qrcode-drop-zone>
      <div class="code-button">
        <button @click="switchCamera"></button>
        <button @click="ClickFlash"></button>
        <button @click="turnCameraOff"></button>
      </div>
    </div>
  </div>
</template>

<script>
// vue-qrcode-reader
import {QrcodeStream, QrcodeDropZone, QrcodeCapture} from 'vue-qrcode-reader'

export default {
  props: {
    camera: {
      type: String,
      default: 'rear',
    },
    torchActive: {
      type: Boolean,
      default: false,
    },
    qrcode: {
      type: Boolean,
      default: false,
    },
    noStreamApiSupport: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    // 
    QrcodeStream,
    QrcodeDropZone,
    QrcodeCapture,
  },
  methods: {
    // 
    onDecode(result) {
      this.$emit('onDecode', result)
    },
    // 
    switchCamera() {
      this.$emit('switchCamera')
    },
    // 
    turnCameraOff() {
      this.$emit('turnCameraOff')
    },
    // 
    ClickFlash() {
      this.$emit('ClickFlash')
    },
    // 
    onInit(promise) {
      this.$emit('onInit', promise)
    },
  },
}
</script>
